 <template>
  <div
    class="media"
    :class="{'mediahover': this.isItemHover}"
    @mouseenter="handleMediaMouseEnter"
    @mouseleave="handleMediaMouseLeave"
  >
    <!-- Thumbnail -->
    <div class="media-left">
      <figure>
        <img
          src="../assets/i_file_icon.png"
          srcset="../assets/i_file_icon.png 1x, ../assets/i_file_icon_2x.png 2x"
          alt="File Icon"
        />
        <p class="file-format">{{ extractFileFormat(listItem.H.FN) }}</p>
      </figure>
    </div>

    <!-- Progressbar and Info -->
    <div class="media-content">
      <div class="upload-content" v-show="!requireAPassword && !needsToRename">
        <p class="filename body-2">
          <img
            class="password-sign"
            src="../assets/i_lock_wh_10px.png"
            alt="Lock icon"
            srcset="../assets/i_lock_wh_10px.png 1x, ../assets/i_lock_wh_10px.png 2x"
            v-show="listItem.H.PAS"
          />
          {{ listItem.H.FN }}
        </p>
        <p
          class="upload-info body-3"
        >{{this.copyCaption ? 'Link copied.' : "Uploaded " + extractUploadTime(listItem.H.TS) + " age." }}</p>
      </div>

      <div class="password-block" v-show="requireAPassword" @keyup.enter="handleForwardClick">
        <input
          ref="passwordInput"
          type="password"
          placeholder="Enter file password"
          class="body-2"
          v-model="password"
        />
      </div>

      <div class="rename-block" v-show="needsToRename" @keyup.enter="handleForwardClick">
        <input
          ref="renameInput"
          type="text"
          placeholder="Rename your File"
          class="body-2"
          v-model="fileName"
        />
      </div>
    </div>
    <!-- Functional button -->
    <div class="media-right">
      <!-- More button -->
      <a
        href="#"
        class="buttons more"
        @click="handleMoreButtonClick"
        v-show="isItemHover && !requireAPassword && !needsToRename"
      >
        <img
          class="logo"
          src="../assets/more_hor_24px.png"
          srcset="../assets/more_hor_24px.png, ../assets/more_hor_24px_2x.png 2x"
          alt="More Button"
        />
        <MenuPanel v-show="isMoreClicked" ref="menuPanel">
          <div class="divider-bottom">
            <button class="menu-item link-2" @click="handleCopyLink">Copy link</button>
          </div>
          <button class="menu-item link-2" @click="handleAddPassword">{{ passwordCaption }}</button>
          <button class="menu-item link-2" @click="handleRenameItems">Rename</button>
          <button class="menu-item link-2" @click="handleDelete">Delete</button>
        </MenuPanel>
      </a>
      <!-- Copy button -->
      <div class="tooltips">
        <a
          href="#"
          class="buttons copy"
          id="copyToClipboard"
          v-show="!requireAPassword && !needsToRename"
          @click="handleCopyLink($event)"
        >
          <img
            class="logo"
            src="../assets/i_copy_24px.png"
            srcset="../assets/i_copy_24px.png, ../assets/i_copy_24px_2x.png 2x"
            alt="Copy Button"
          />
        </a>
        <!-- Tooltip -->
        <div class="tooltip-text" v-show="showTooltip">
          <div class="text-block">
            <p class="body-3">
              <span class="subtitle-2">Tap to copy private-link.</span> Share it with anyone to access the file.
            </p>
          </div>
        </div>
      </div>
      <!-- Forward -->
      <button
        class="buttons forward"
        @click="handleForwardClick"
        v-show="requireAPassword || needsToRename"
      >
        <img
          class="logo"
          src="../assets/arrow_forward_24px.png"
          srcset="../assets/arrow_forward_24px.png 1x, ../assets/arrow_forward_24px_2x.png 2x"
          alt="Forward Button"
        />
      </button>
    </div>
  </div>
</template>

<script>
import MenuPanel from "../components/MenuPanel";
export default {
  name: "List-item",
  components: {
    MenuPanel
  },
  props: {
    listItem: Object,
    index: Number,
    requireAPassword: Boolean,
    needsToRename: Boolean,
    showTooltip: Boolean,
    isMoreClicked: Boolean,
    isItemHover: Boolean,
    copyCaption: Boolean
  },
  data() {
    return {
      isMoreButtonClicked: false,
      password: null,
      fileName: "",
      passwordCaption: "Set password"
    };
  },
  mounted() {
    this.fileName = this.listItem.H.FN;
  },
  methods: {
    handleMediaMouseLeave() {
      if (this.isMoreClicked) return;
      // this.isHover = false;
      this.$emit("hover-cancel", this.index);
    },

    handleMediaMouseEnter() {
      if (this.isMoreClicked) return;
      this.$emit("hover", this.index);
    },

    handleMoreButtonClick(event) {
      event.stopPropagation();
      event.preventDefault();
      if (this.listItem.H.PAS != "") {
        this.passwordCaption = "Remove Password";
      }
      // this.isMoreButtonClicked = true;
      this.$emit("more-clicked", this.index);
    },

    handleFormContextMenu() {
      if (this.isMoreClicked) return;
      this.$emit("hover-cancel", this.index);
    },

    handleCopyLink(event) {
      event.stopPropagation();
      event.preventDefault();
      this.$emit("more-clicked-cancel");
      this.$emit("copy-link", this.index);
    },
    handleDelete(event) {
      event.stopPropagation();
      event.preventDefault();
      this.$emit("more-clicked-cancel");
      this.$emit("delete", this.index);
    },
    extractFileFormat(fn) {
      let idx = fn.lastIndexOf(".");
      if (idx == -1) return "UNK";
      // Limit File Format to 4 charrecter
      else return fn.substr(idx + 1, 4);
    },
    extractUploadTime(ts) {
      let seconds = (Date.now() - ts) / 1000;
      if (seconds >= 60) {
        let minutes = seconds / 60;
        if (minutes >= 60) {
          let hours = minutes / 60;
          if (hours >= 24) {
            let days = minutes / 24;
            return Math.round(days) + " days";
          } else {
            return Math.round(hours) + " hours";
          }
        } else {
          return Math.round(minutes) + " minutes";
        }
      } else {
        return Math.round(seconds) + " seconds";
      }
    },

    handleForwardClick() {
      if (this.requireAPassword) {
        this.$emit("password-done", {
          index: this.index,
          password: this.password
        });
      }

      if (this.needsToRename) {
        this.$emit("rename-done", { index: this.index, name: this.fileName });
      }
    },

    handleRenameItems() {
      this.$emit("rename", this.index);
      this.$emit("password-cancel");
      setTimeout(() => {
        this.$refs.renameInput.focus();
      }, 1);
    },

    handleAddPassword() {
      this.$emit("password", this.index);
      this.$emit("rename-cancel");
      setTimeout(() => {
        this.$refs.passwordInput.focus();
      }, 1);
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

.media {
  max-width: 480px;
  min-width: 360px;
  height: 88px;

  &.mediahover {
    background-color: $wh-ov04;

    .more {
      display: flex;
    }
  }

  @include until($tablet) {
    min-width: 360px;
    height: 80px;
  }

  @include until(361px) {
    min-width: 320px;
    height: 80px;
  }
}

.media-left {
  height: 100%;
  margin: 0;
  display: flex;

  figure {
    position: relative;
    margin: auto 0 auto 33px;

    @include until(361px) {
      margin: auto 0 auto 17px;
    }

    @include until(360px) {
      display: none;
    }

    img {
      display: block;
      width: 38px;
      height: 50px;
    }
  }

  .file-format {
    position: absolute;
    top: 29px;
    left: 50%;
    transform: translate(-50%, 0);
    color: $wm;
  }

  .add-hover {
    background-color: $bl;
  }
}

.media-content {
  height: 100%;
  margin: 0;
  display: flex;

  .upload-content {
    margin: 0 0 0 17px;
    padding: 16px 0 24px 0;
    min-width: 0;
    flex: 1;
    max-width: 262px;

    @include until($tablet) {
      margin: 0 0 0 17px;
      padding: 12px 0 16px 0;
    }

    @include until(361px) {
      margin: 0 0 0 17px;
      padding: 12px 0 16px 0;
    }

    @include until(360px) {
      margin: 0 0 0 22px;
      padding: 12px 0 16px 0;
    }
  }

  .filename {
    color: $wh;
    margin: 0 0 2px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    .password-sign {
      display: inline-block;
      width: 13px;
      height: 12px;
    }
  }

  .upload-info {
    color: $wm;
    margin: 0 0 8px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .upload-text {
    margin: auto 0 auto 17px;
    color: $wm;

    @include until($tablet) {
      margin: auto 0 auto 14px;
    }
  }

  .text-hover {
    color: $wh;
  }

  .password-block,
  .rename-block {
    margin: 0 0 0 16px;
    padding: 16px 0 15px;
    min-width: 0;
    flex: 1;
    display: flex;

    input[type="password"],
    input[type="text"] {
      margin: 0 0 2px;
      width: 100%;
      height: 32px;
      background-color: $bm;
      border: none;
      border-radius: 5px;
      padding: 0 7px;
      outline: none;
      color: $wh;
      caret-color: $p1;
      opacity: 0.6;
      transition: opacity 150ms ease-in-out;
      align-self: center;

      &:hover {
        opacity: 1;
      }

      &:focus {
        opacity: 1;
      }

      &::selection {
        background-color: rgba(32, 169, 188, 0.38);
      }

      &::placeholder {
        color: $wl;
      }
    }
  }
}

.media-right {
  margin: auto 24px auto 24px;
  height: 32px;
  display: flex;

  @include until(361px) {
    margin: auto 16px auto 24px;
  }

  @include until(360px) {
    margin: auto 18px auto 24px;
  }

  @include from($tablet) {
    margin: auto 24px auto 24px;
  }

  .buttons[disabled] {
    background-color: transparent;
    img {
      opacity: 0.2;
    }
    &:hover {
      background-color: transparent;
      img {
        opacity: 0.2;
      }
    }

    &:active {
      background-color: transparent;
    }
  }

  .buttons {
    border-radius: 5px;
    width: 32px;
    height: 32px;
    padding: 0;
    background-color: transparent;
    border: none;
    outline: none;
    transition: background-color 150ms ease-in-out;
    display: flex;

    img {
      margin: auto;
      width: 24px;
      height: 24px;
      opacity: 0.5;
      transition: opacity 150ms ease-in-out;
    }

    &:hover {
      background-color: $bm;
      img {
        opacity: 1;
      }
    }

    &:active {
      background-color: $bl;
    }

    &.copy {
      margin: 0 0 0 16px;
    }

    &.forward {
      margin: auto 0 auto 20px;
    }

    &.more {
      position: relative;
    }
  }
}

.tooltips {
  position: relative;
}

.tooltip-text {
  position: absolute;
  left: -6px;
  bottom: 46px;
  width: 256px;
  height: 65px;
  background-color: $p1;
  display: flex;
  @include until($tablet) {
    left: auto;
    right: -14px;
    bottom: 48px;
  }

  .text-block {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      bottom: -6px;
      left: 32px;
      width: 12px;
      height: 12px;
      background-color: $p1;
      transform: rotate(45deg);
      @include until($tablet) {
        left: 221px;
        bottom: -6px;
      }
    }
  }

  span,
  p {
    display: inline-block;
    color: $bh;
    margin: auto;
  }

  p {
    padding: 14px 20px 17px;
  }
}
</style>