<template>
  <div id="uploader" class="columns is-centered" ref="fileform">
    <input type="text" class="copyInput" ref="copyInput" />
    <div class="column is-6-desktop is-12-tablet is-12-mobile is-paddingless">
      <div class="container">
        <form          
          class="form"
          :class="{'formhover': isHover}"
          @mouseenter="handleFormMouseEnter"
          @mouseleave="handleFormMouseLeave"
        >
          <input
            type="file"
            id="directory"
            v-show="!isUpload"
            ref="directory"
            webkitdirectory
            mozdirectory
            directory
            multiple
            v-on:change="handleDirectoryUpload()"
          />

          <input
            type="file"
            id="file"
            v-show="!isUpload"
            ref="file"
            multiple
            v-on:change="handleFileUpload()"
          />

          <div class="media">
            <!-- Thumbnail -->
            <div class="media-left">
              <div class="add-file" v-show="!isUpload">
                <img
                  src="../assets/i_add_wm_24px.png"
                  alt="Plus Icon"
                  srcset="../assets/i_add_wm_24px.png 1x, ../assets/i_add_wm_24px_2x.png 2x"
                  class="add-icon"
                />
              </div>
              <figure v-show="isUpload">
                <img
                  src="../assets/i_file_icon.png"
                  srcset="../assets/i_file_icon.png 1x, ../assets/i_file_icon_2x.png 2x"
                  alt="File Icon"
                />
                <p class="file-format">{{ fileFormat.substr(0,4) }}</p>
              </figure>
            </div>

            <!-- Progressbar and Info -->
            <div class="media-content">
              <p class="upload-text body-2" v-show="!isUpload">Add your file to start uploading.</p>
              <div class="upload-content" v-show="isUpload">
                <p class="filename body-2">{{ fileName }}</p>
                <p class="upload-info body-3">{{ uploadInfo }}</p>
                <div class="progressbar" v-show="beShown">
                  <span
                    class="download bar"
                    :class="{ 'is-stuck': isStuck }"
                    :style="{ width: uploadPercent + '%' }"
                  ></span>
                </div>
              </div>
            </div>
            <!-- Functional button -->
            <div class="media-right tooltips">
              <!-- More button -->
              <a
                href="#"
                class="buttons more"
                ref="menuPanel"
                @click="handleMoreButtonClick($event)"
              >
                <img
                  class="logo"
                  src="../assets/more_hor_24px.png"
                  srcset="../assets/more_hor_24px.png, ../assets/more_hor_24px_2x.png 2x"
                  alt="More Button"
                />
                <MenuPanel v-show="isMoreButtonClicked">
                  <button
                    v-show="!isUpload"
                    class="menu-item link-2"
                    @click="openFileDialog($event)"
                  >Upload File</button>
                  <button
                    v-show="!isUpload"
                    class="menu-item link-2"
                    @click="openDirectoryDialog($event)"
                  >Upload Folder</button>
                  <button
                    v-show="buttonStatus === 'cancel' && isUpload && processing"
                    class="menu-item link-2"
                    @click="btnCancelUpload"
                  >Cancel</button>
                  <button
                    v-show="isUpload && uploader.url"
                    class="menu-item link-2"
                    @click="copyUploaderLink"
                  >Copy link</button>
                </MenuPanel>
              </a>
              <!-- Cancel button -->
              <a
                href="#"
                @click="btnCancelUpload"
                class="buttons cancel"
                :disabled="processing"
                v-show="buttonStatus === 'cancel' && isUpload"
              >
                <img
                  src="../assets/i_close_24px.png"
                  srcset="../assets/i_close_24px.png 1x, ../assets/i_close_24px_2x.png 2x"
                  alt="Forward button"
                />
              </a>
              <!-- Copy button -->
              <!-- <a
                href="#"
                class="buttons"
                :disabled="isDisabled"
                id="copyToClipboard"
                v-show="buttonStatus === 'copy'"
                @mouseenter="onMouseEnter"
                @mouseleave="onMouseLeave"
              >
                <img
                  class="logo"
                  src="../assets/i_copy_24px.png"
                  srcset="../assets/i_copy_24px.png, ../assets/i_copy_24px_2x.png 2x"
                  alt="Copy Button"
                />
              </a>-->
              <!-- Tooltip -->
              <!-- <div class="tooltip-text" v-show="showTooltip">
                <div class="text-block">
                  <p class="body-3">
                    <span class="subtitle-2">Tap to copy private-link.</span> Share it with anyone to access the file.
                  </p>
                </div>
              </div>-->
            </div>
          </div>
        </form>
        <div id="list" v-show="uploadList">
          <UploaderListItem
            v-for="(listItem, index) in uploadList"
            :key="index"
            ref="listItem"
            :index="index"
            :listItem="listItem"
            :showTooltip="showTooltip == index"
            :requireAPassword="requireAPassword == index"
            :needsToRename="needsToRename == index"
            :isMoreClicked="isMoreClicked == index"
            :isItemHover="isItemHover === index"
            :copyCaption="copyCaption === index"
            @rename="needsToRename = index"
            @password="handlePassword"
            @password-cancel="requireAPassword = -1"
            @rename-cancel="needsToRename = -1"
            @password-done="handlePasswordDone"
            @rename-done="handleRename"
            @more-clicked="isMoreClicked = index"
            @more-clicked-cancel="isMoreClicked = -1;"
            @hover="handleHoverItems"
            @hover-cancel="handleCancelHover"
            @copy-link="handleCopyLink($event)"
            @delete="handleDelete($event)"
          ></UploaderListItem>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BHUploader from "../uploader";
import { userSession } from "../user";
import clipboard from "clipboard";
import { setTimeout, clearTimeout } from "timers";
import UploaderListItem from "../components/UploaderListItem";
import MenuPanel from "../components/MenuPanel";
export default {
  name: "upload",
  components: {
    UploaderListItem,
    MenuPanel
  },

  data() {
    return {
      // isDisabled: false,
      fileFormat: "...",
      fileName: "Getting file information...",
      fileSize: 0,
      uploadInfo: "Loading...",
      speedInfo: "Loading...",
      uploadPercent: 0,
      lastPercent: 0,
      isHover: false,

      buttonStatus: "cancel",
      beShown: true,
      isStuck: false,

      isUpload: false,
      // Confirm that browser is drag and drop capable
      dragAndDropCapable: false,
      uploader: new BHUploader(
        userSession.loadUserData().hubUrl,
        userSession.loadUserData().appPrivateKey
      ),

      showTooltip: -1,
      processing: false,
      tooltipOnHover: null,

      uploadList: [],

      isMoreButtonClicked: false,
      isMoreClicked: -1,
      isItemHover: -1,
      requireAPassword: -1,
      needsToRename: -1,
      copyCaption: -1
    };
  },

  mounted() {
    // userSession.listFiles((name) => userSession.deleteFile(name));
    if (localStorage.getItem("first_copy") == "true") {
      this.$emit("display-banner", 0);
    }
    this.uploader.onStart = async (fileName, fileSize) => {
      this.isUpload = true;
      this.fileName = fileName;
      this.fileSize = fileSize;
      let fx = fileName.substr(fileName.lastIndexOf("."));
      if (fx[0] == ".") fx = fx.substr(1);
      this.fileFormat = fx;
      this.uploadInfo = "Uploading 0 of " + this.byteToNormalString(fileSize);
      this.uploadPercent= 0;
    };

    this.uploader.onProgress = async (loaded, total, processing) => {
      if (processing) {
        // console.log("process")
        this.isUpload = true;
        this.uploadPercent = Math.floor((loaded * 100) / total);
        this.uploadInfo = "Processing...";
        this.processing = true;
      } else {
        this.uploadInfo =
          "Uploading " +
          this.byteToNormalString(loaded) +
          " of " +
          this.byteToNormalString(total);
        this.uploadPercent = Math.floor((loaded * 100) / total);
      }
    };

    this.uploader.onFinish = async (url, item) => {
      // this.buttonStatus = "copy";
      // this.uploadInfo = "Upload complete.";
      // this.beShown = false;
      let idx = this.uploadList.unshift(item);
      this.isUpload = false;
      this.showTooltip =
        localStorage.getItem("tooltip") == undefined ||
        Number.parseInt(localStorage.getItem("tooltip")) < 2
          ? idx - 1
          : -1;
    };

    this.dragAndDropCapable = this.determineDragAndDropCapable();

    if (this.dragAndDropCapable && !this.isUpload) {
      const dragEvents = ["drag", "dragstart", "dragend", "dragover", "drop"];
      dragEvents.forEach(
        function(event) {
          this.$refs.fileform.addEventListener(
            event,
            function(e) {
              e.preventDefault();
              e.stopPropagation();
            }.bind(this),
            false
          );
        }.bind(this)
      );

      this.$refs.fileform.addEventListener(
        "dragenter",
        function() {
          this.isHover = true;
        }.bind(this)
      );
      this.$refs.fileform.addEventListener(
        "dragleave",
        function() {
          this.isHover = false;
        }.bind(this)
      );
      this.$refs.fileform.addEventListener(
        "drop",
        function(e) {
          if (this.isUpload) return;
          this.uploader.processDragedFiles(e.dataTransfer, result => {
            if (result.done) {
              if (result.error != "") {
                this.fileName = result.fileName;
                this.isUpload = true;
                this.beShown = false;
                this.uploadInfo = "File size limited to 512 MB.";
                this.buttonStatus = "";
                return;
              } else {
                // this.uploader.setPassword("123");
                this.uploader.canceled = false;                
                this.uploader.upload(result.fileName);
              }
            }
          });
        }.bind(this)
      );
    }

    let cb = new clipboard("#copyToClipboard", {
      text: () => {
        return this.uploader.url;
      }
    });

    cb.on("success", () => {
      localStorage.setItem("first_copy", "true");
      if (localStorage.getItem("tooltip") == undefined) {
        localStorage.setItem("tooltip", "1");
      } else {
        localStorage.setItem(
          "tooltip",
          Number.parseInt(localStorage.getItem("tooltip")) + 1
        );
      }
      this.$emit("display-banner", 5000);
      // this.isDisabled = true;
      this.uploadInfo = "Link copied, you can share it with anyone.";
      this.showTooltip = -1;
      setTimeout(() => {
        // this.isDisabled = false;
        this.uploadInfo = "Upload complete.";
      }, 3000);
    });

    this.loadUserFile();
  },

  methods: {
    handlePassword(index) {
      if (this.uploadList[index].H.PAS != "") {
        this.uploadList[index].H.PAS = "";
        this.uploader.changePassword(
          this.uploadList[index].HFN,
          this.uploadList[index].H,
          ""
        );
      } else {
        this.requireAPassword = index;
      }
    },
    handlePasswordDone(e) {
      this.isMoreClicked = -1;
      this.isMoreButtonClicked = false;
      // this.uploadList[e.index].Disabled = true;
      this.requireAPassword = -1;
      this.uploadList[e.index].H.PAS = e.password;
      this.uploader.changePassword(
        this.uploadList[e.index].HFN,
        this.uploadList[e.index].H,
        e.password
      );
      // this.uploadList[e.index].Disabled = false;
    },
    async handleRename(e) {
      this.isMoreClicked = -1;
      this.isMoreButtonClicked = false;
      this.uploadList[e.index].H.FN = e.name;
      // this.uploadList[e.index].Disabled = true;
      this.needsToRename = -1;

      await this.uploader.renameFile(
        this.uploadList[e.index].HFN,
        this.uploadList[e.index].H,
        e.name
      );

      // this.uploadList[e.index].Disabled = false;
    },
    handleCopyLink(index) {
      if (index >= 3) return;
      let item = this.uploadList[index];
      let url = this.uploader.getURL(item.H);
      this.$refs["copyInput"].value = url;
      this.$refs["copyInput"].style = "visibility: visible";
      this.$refs["copyInput"].select();
      document.execCommand("copy");
      this.$refs["copyInput"].style = "visibility: hidden";
      // To Show Link copied. caption for 3 seconds
      this.copyCaption = index;
      setTimeout(() => {
        this.copyCaption = -1;
      }, 3000);
    },
    copyUploaderLink() {      
      let url = this.uploader.url;
      this.$refs["copyInput"].value = url;
      this.$refs["copyInput"].style = "visibility: visible";
      this.$refs["copyInput"].select();
      document.execCommand("copy");
      this.$refs["copyInput"].style = "visibility: hidden";
      // To Show Link copied. caption for 3 seconds
      // this.copyCaption = index;
      // setTimeout(() => {
      //   this.copyCaption = -1;
      // }, 3000);
    },
    handleDelete(index) {
      this.isMoreClicked = -1;
      this.isMoreButtonClicked = false;
      let item = this.uploadList[index];
      this.uploadList.splice(index, 1);
      this.uploader.deleteFile(item.H).then(() => {
        userSession.deleteFile(item.HFN);
      });
    },
    loadUserFile() {
      userSession.listFiles(name => {
        userSession.getFile(name, { decrypt: true }).then(async e => {
          if (e) {
            let H = JSON.parse(e);
            if (H.BHV == 7) {
              H = await this.uploader.upgradeHistory(name, H);
            }
            let item = {
              HFN: name,
              H
            };
            this.uploadList.push(item);
            this.uploadList.sort((a, b) => {
              if (a.H.TS > b.H.TS) return -1;
              if (a.H.TS < b.H.TS) return 1;
              if ((a.H.TS = b.H.TS)) return 0;
            });
          }
        });
        return true;
      });
    },
    btnCancelUpload() {
      if (this.processing) return;
      this.uploader.canceled = true;
      this.isUpload = false;
    },

    onMouseEnter() {
      // if (!this.isDisabled) {
      this.uploadInfo = "Copy share link to clipboard.";
      this.tooltipOnHover = setTimeout(() => {
        this.showTooltip = -1;
      }, 500);
      // }
    },

    onMouseLeave() {
      // if (!this.isDisabled) {
      this.uploadInfo = "Upload complete.";
      if (this.showTooltip) {
        this.showTooltip = -1;
      }
      clearTimeout(this.tooltipOnHover);
      this.tooltipOnHover = null;
      // }
    },

    byteToNormalString(input) {
      if (input + 100 * 1024 * 1024 > 1024 * 1024 * 1024) {
        input = input / (1024 * 1024 * 1024);
        return input.toFixed(1) + " GB";
      } else if (input + 100 * 1024 > 1024 * 1024) {
        input = input / (1024 * 1024);
        return input.toFixed(1) + " MB";
      } else if (input + 100 > 1024) {
        return (input / 1024).toFixed(1) + " KB";
      } else {
        return input + " B";
      }
    },

    // Determines if the drag and drop functionality is in the window
    determineDragAndDropCapable() {
      // Create a test element to see if certain events are present that let us do drag and drop.
      var div = document.createElement("div");
      return (
        ("draggable" in div || ("ondragstart" in div && "ondrop" in div)) &&
        "FormData" in window &&
        "FileReader" in window
      );
    },

    handleFileUpload() {
      if (this.isUpload) return;
      this.uploader.processDragedFiles(this.$refs.file, result => {
        if (result.done) {
          if (result.error != "") {
            this.fileName = result.fileName;
            this.isUpload = true;
            this.beShown = false;
            this.uploadInfo = "File size limited to 512 MB.";
            this.buttonStatus = "";
            return;
          } else {
            // this.uploader.setPassword("123");
            this.uploader.canceled = false;
            this.uploader.upload(result.fileName);
          }
        }
      });
    },

    handleDirectoryUpload() {
      if (this.isUpload) return;
      this.uploader.processDragedFiles(this.$refs.directory, result => {
        if (result.done) {
          if (result.error != "") {
            this.fileName = result.fileName;
            this.isUpload = true;
            this.beShown = false;
            this.uploadInfo = "File size limited to 512 MB.";
            this.buttonStatus = "";
            return;
          } else {
            // this.uploader.setPassword("123");
            this.uploader.canceled = false;
            this.uploader.upload(result.fileName);
          }
        }
      });
    },

    openFileDialog(event) {
      this.isMoreButtonClicked = false;
      event.stopPropagation();
      event.preventDefault();
      this.$refs.file.click();
    },

    openDirectoryDialog(event) {
      this.isMoreButtonClicked = false;
      event.stopPropagation();
      event.preventDefault();
      this.$refs.directory.click();
    },

    handleFormMouseLeave() {
      if (this.isMoreButtonClicked) return;
      this.isHover = false;
    },

    handleFormMouseEnter() {
      this.isHover = true;
      this.isMoreClicked = -1;
      this.isItemHover = -1;
    },

    handleMoreButtonClick(event) {
      event.stopPropagation();
      event.preventDefault();
      this.isMoreButtonClicked = true;
    },

    handleFormContextMenu() {
      if (this.isMoreButtonClicked) return;
      this.isHover = false;
    },

    handleDocumentClick(event) {
      if (this.isMoreButtonClicked) {
        const menuPanel = this.$refs.menuPanel.$el;
        let target = event.target;
        if (menuPanel !== target || !menuPanel.contains(target)) {
          this.isMoreButtonClicked = false;
          this.isHover = false;
        }
      }

      if (this.isMoreClicked !== -1 && this.isItemHover !== -1) {
        const listItemMenu = this.$refs.listItem[this.isMoreClicked]
          .$children[0].$el;
        let target = event.target;
        if (listItemMenu !== target && !listItemMenu.contains(target)) {
          this.isMoreClicked = -1;
          const listItem = this.$refs.listItem[this.isItemHover].$el;
          if (listItem !== target && !listItem.contains(target)) {
            this.isItemHover = -1;
          }
        }
      }

      if (this.requireAPassword !== -1) {
        const listItem = this.$refs.listItem[this.requireAPassword].$el;
        let target = event.target;
        if (listItem !== target && !listItem.contains(target)) {
          this.requireAPassword = -1;
        }
      }

      if (this.needsToRename !== -1) {
        const listItem = this.$refs.listItem[this.needsToRename].$el;
        let target = event.target;
        if (listItem !== target && !listItem.contains(target)) {
          this.needsToRename = -1;
        }
      }
    },

    handleEscClicked(event) {
      if (event.keyCode === 27) {
        this.isMoreClicked = -1;
        this.needsToRename = -1;
        this.requireAPassword = -1;
        this.isMoreButtonClicked = false;
      }
    },

    handleHoverItems(index) {
      this.isHover = false;
      this.isMoreButtonClicked = false;
      this.isItemHover = index;
    },

    handleCancelHover() {
      this.isItemHover = -1;
      this.isMoreClicked = -1;
    }
  },

  created() {
    document.addEventListener("click", this.handleDocumentClick);
    document.addEventListener("keydown", this.handleEscClicked);
  },
  destroyed() {
    // important to clean up!!
    document.removeEventListener("click", this.handleDocumentClick);
    document.removeEventListener("keydown", this.handleEscClicked);
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

.container {
  padding: 8px 0;
  border: 1px solid $bm;
  background-color: $bh;
  box-sizing: border-box;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.16),
    0px 10px 20px rgba(0, 0, 0, 0.24);

  margin: 0 auto;
  max-width: 480px;
  min-width: 320px;
}

.media {
  max-width: 480px;
  min-width: 360px;
  height: 88px;
  border: none !important;
  margin: 0 !important;
  padding: 0 !important;

  @include until($tablet) {
    min-width: 360px;
    height: 80px;
  }

  @include until(361px) {
    min-width: 320px;
    height: 80px;
  }
}

.media-left {
  height: 100%;
  margin: 0;
  display: flex;

  figure {
    position: relative;
    margin: auto 0 auto 29px;

    @include until(361px) {
      margin: auto 0 auto 17px;
    }

    @include until(360px) {
      display: none;
    }

    img {
      display: block;
      width: 38px;
      height: 50px;
    }
  }

  .file-format {
    position: absolute;
    top: 29px;
    left: 50%;
    transform: translate(-50%, 0);
    color: $wm;
  }

  .add-file {
    width: 44px;
    height: 44px;
    display: flex;
    border: 1px solid $bl;
    border-radius: 5px;
    margin: auto 0 auto 30px;

    @include until(361px) {
      margin: auto 0 auto 14px;
    }

    .add-icon {
      margin: auto;
      opacity: 0.38;
      img {
        width: 24px;
        height: 24px;
      }
    }
  }

  .add-hover {
    background-color: $bl;
  }
}

.media-content {
  height: 100%;
  margin: 0;
  display: flex;

  .upload-content {
    margin: 0 0 0 17px;
    padding: 16px 0 24px 0;
    min-width: 0;
    flex: 1;
    max-width: 262px;

    @include until($tablet) {
      margin: 0 0 0 17px;
      padding: 12px 0 16px 0;
    }

    @include until(361px) {
      margin: 0 0 0 17px;
      padding: 12px 0 16px 0;
    }

    @include until(360px) {
      margin: 0 0 0 22px;
      padding: 12px 0 16px 0;
    }
  }

  .filename {
    color: $wh;
    margin: 0 0 2px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .upload-info {
    color: $wm;
    margin: 0 0 8px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .upload-text {
    margin: auto 0 auto 17px;
    color: $wm;

    @include until($tablet) {
      margin: auto 0 auto 14px;
    }
  }

  .text-hover {
    color: $wh;
  }
}

.progressbar {
  position: relative;
  height: 4px;
  width: 100%;
  background-color: $bm;

  .bar {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    transition: width 150ms ease-in-out;
  }

  .download {
    background-color: $p1;
  }
}

.media-right {
  margin: auto 24px auto 24px;
  width: auto;
  height: 32px;
  display: flex;

  @include until(361px) {
    margin: auto 16px auto 24px;
  }

  @include until(360px) {
    margin: auto 18px auto 24px;
  }

  @include from($tablet) {
    margin: auto 28px auto 24px;
  }

  .buttons[disabled] {
    background-color: transparent;
    img {
      opacity: 0.2;
    }
    &:hover {
      background-color: transparent;
      img {
        opacity: 0.2;
      }
    }

    &:active {
      background-color: transparent;
    }
  }

  .buttons {
    border-radius: 5px;
    width: 32px;
    height: 32px;
    padding: 0;
    background-color: transparent;
    border: none;
    outline: none;
    transition: background-color 150ms ease-in-out;
    display: flex;

    img {
      margin: auto;
      width: 24px;
      height: 24px;
      opacity: 0.5;
      transition: opacity 150ms ease-in-out;
    }

    &:hover {
      background-color: $bm;
      img {
        opacity: 1;
      }
    }

    &:active {
      background-color: $bl;
    }

    &.cancel {
      margin: 0 0 0 16px;
    }

    &.more {
      display: none;
    }
  }
}

