<template>
  <div id="app-upload">
    <div class="background-color">
      <div class="radial-gradient">
        <div class="dithering"></div>
      </div>
    </div>
    <div ref="contentLimiter" class="contents-limiter">
      <div class="app-block columns">
        <!-- Navbar -->
        <nav class="app-navbar column is-12 is-narrow-mobile">
          <router-link to="/web" class="logo">
            <img
              @click="clickOnLogo()"
              src="../assets/‌logo_24_wm.png"
              srcset="../assets/‌logo_24_wm.png 1x, ../assets/‌logo_24_wm_2x.png 2x"
              alt="BlackHole Logo"
            />
          </router-link>

          <div class="navbar-right">
            <!-- App Launcher -->
            <div ref="appLauncher" class="app-launcher" @click.stop="handleAppLauncherClick">
              <img
                src="../assets/apps_24px.png"
                alt="App Launcher icon"
                srcset="../assets/apps_24px.png 1x, ../assets/apps_24px_2x.png 2x"
              />
              <AppLauncher v-show="isAppLauncherOpen"></AppLauncher>
            </div>

            <!-- Logout Panel -->
            <div ref="userAvatar" class="user-avatar" @click.stop="handleLogoutPanelClick">
              <img
                src="../assets/person_24px.png"
                alt="User Avatar"
                srcset="../assets/person_24px.png 1x, ../assets/person_24px_2x.png 2x"
              />
              <LogoutPanel
                v-show="isLoginPanelOpen"
                :userName="setUserName"
                @logout="logOutFromBlockstack"
              >
                <div class="user-avatar">
                  <img
                    src="../assets/person_24px.png"
                    alt="User Avatar"
                    srcset="../assets/person_24px.png 1x, ../assets/person_24px_2x.png 2x"
                  />
                </div>
              </LogoutPanel>
            </div>
          </div>
        </nav>
        <!-- Main content -->
        <main id="main" class="column is-12 is-narrow-mobile">
          <router-view @display-banner="doDisplay($event)"></router-view>
        </main>
        <!-- Footer -->
        <footer ref="footer" class="app-footer column is-12 is-narrow-mobile">
          <div class="columns is-vcentered">
            <div class="right column is-12">
              <p class="copyright overline">©2019 blackhole.</p>
              <router-link to="/legal" class="legal link-3">legal</router-link>
            </div>
          </div>
        </footer>
        <!-- Banner -->
        <div ref="banner" class="banner is-hidden-mobile" v-show="isDisplayed">
          <div class="relative">
            <span class="close" @click="doClose()">
              <img
                src="../assets/i_close_op50_24px.png"
                alt="Close button"
                srcset="../assets/i_close_op50_24px.png 1x, ../assets/i_close_op50_24px_2x.png 2x"
              />
            </span>
            <div class="os-icon">
              <img
                src="../assets/i_windows_24px.png"
                srcset="../assets/i_windows_24px.png 1x, ../assets/i_windows_24px_2x.png 2x"
                alt="Windows icon"
                v-show="osName === 'Windows'"
              />
              <img
                src="../assets/i_mac_24px.png"
                srcset="../assets/i_mac_24px.png 1x, ../assets/i_mac_24px_2x.png 2x"
                alt="Mac icon"
                v-show="osName === 'MacOS'"
              />
            </div>
            <h6 class="h6" style="text-align:center">Share files right from your desktop.</h6>
            <ul class="list">
              <li class="item">
                <img
                  src="../assets/i_done_op54_24px.png"
                  alt="Done icon"
                  srcset="../assets/i_done_op54_24px.png 1x, ../assets/i_done_op54_24px_2x.png 2x"
                />
                <span class="body-3">Upload hotkey</span>
              </li>
              <li class="item">
                <img
                  src="../assets/i_done_op54_24px.png"
                  alt="Done icon"
                  srcset="../assets/i_done_op54_24px.png 1x, ../assets/i_done_op54_24px_2x.png 2x"
                />
                <span class="body-3">Quicker upload</span>
              </li>
              <li class="item">
                <img
                  src="../assets/i_done_op54_24px.png"
                  alt="Done icon"
                  srcset="../assets/i_done_op54_24px.png 1x, ../assets/i_done_op54_24px_2x.png 2x"
                />
                <span class="body-3">Upload multiple folders</span>
              </li>
            </ul>
            <div class="download-link">
              <router-link
                to="/select-os"
                class="button-2"
                v-show="osName === 'Windows'"
              >Get the windows app</router-link>
              <router-link
                to="/select-os"
                class="button-2"
                v-show="osName === 'MacOS'"
              >Get the mac app</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { userSession } from "../user";
import { setTimeout } from "timers";
import LogoutPanel from "../components/UploaderLogoutPanel";
import AppLauncher from "../components/UploaderAppLauncher";

export default {
  name: "upload-app",
  components: {
    LogoutPanel,
    AppLauncher
  },

  data() {
    return {
      logText: "log out",
      osName: "",
      // toggle banner display
      isDisplayed: false,
      isLoginPanelOpen: false,
      isAppLauncherOpen: false
    };
  },

  mounted() {
    if (!userSession.isUserSignedIn() && !userSession.isSignInPending())
      this.$router.push("/login");
    this.osDetection();
    this.setBannerTopPosition();
    this.setBannerRightPosition()
  },

  updated(){
    this.setBannerTopPosition();
    this.setBannerRightPosition()
  },

  methods: {
    osDetection() {
      if (navigator.appVersion.indexOf("Win" || "Linux" || "Android") != -1) {
        this.osName = "Windows";
      }
      if (navigator.appVersion.indexOf("Mac" || "iPad" || "iPhone") != -1) {
        this.osName = "MacOS";
      }
    },

    logOutFromBlockstack() {
      userSession.signUserOut();
      this.$router.push("/landing");
    },
    doDisplay(time) {
      setTimeout(() => {
        this.isDisplayed = true;
      }, time);
    },
    clickOnLogo() {
      window.location.reload();
    },
    doClose() {
      this.isDisplayed = false;
      localStorage.removeItem("first_copy");
    },

    handleLogoutPanelClick() {
      this.isAppLauncherOpen = false;
      this.isLoginPanelOpen = true;
    },

    handleAppLauncherClick() {
      this.isAppLauncherOpen = true;
      this.isLoginPanelOpen = false;
    },

    handleDocumentClick(event) {
      if (this.isLoginPanelOpen) {
        const userAvatar = this.$refs.userAvatar;
        let target = event.target;
        if (!userAvatar || !userAvatar.contains(target)) {
          this.isLoginPanelOpen = false;
        }
      }

      if (this.isAppLauncherOpen) {
        const appLauncher = this.$refs.appLauncher;
        let target = event.target;
        if (!appLauncher || !appLauncher.contains(target)) {
          this.isAppLauncherOpen = false;
        }
      }
    },

    handleEscKeyDown(event) {
      if (event.keyCode === 27 && this.isLoginPanelOpen) {
        this.isLoginPanelOpen = false;
      }
      if (event.keyCode === 27 && this.isAppLauncherOpen) {
        this.isAppLauncherOpen = false;
      }
    },

    setBannerTopPosition() {
      let bannerTopHeight = this.$refs.banner.offsetTop + window.scrollY;
      let bannerHeight = this.$refs.banner.clientHeight;
      let footerTopHeight = this.$refs.footer.offsetTop;      
      if (bannerTopHeight + bannerHeight >= footerTopHeight - 40) {
        this.$refs.banner.style.bottom = `${48 + (bannerTopHeight + bannerHeight) - (footerTopHeight - 40)}px`;
      } else {
        this.$refs.banner.style.bottom = "48px";
      }
    },

    setBannerRightPosition() {
      this.$refs.banner.style.right = `${this.$refs.contentLimiter.offsetLeft - 16}px`
    }
  },

  computed: {
    setUserName() {
      if (!userSession.isUserSignedIn()) return "";
      let userId = userSession.loadUserData().username;
      let index = userId.indexOf(".id.blockstack");
      return userId.slice(0, index);
    }
  },

  created() {
    document.addEventListener("click", this.handleDocumentClick);
    document.addEventListener("keydown", this.handleEscKeyDown);
    document.addEventListener("scroll", this.setBannerTopPosition);
    window.addEventListener("resize", this.setBannerRightPosition);
  },
  destroyed() {
    // Important to clean up!!
    document.removeEventListener("click", this.handleDocumentClick);
    document.removeEventListener("keydown", this.handleEscKeyDown);
    document.removeEventListener("scroll", this.setBannerTopPosition);
    window.removeEventListener("resize", this.setBannerRightPosition);
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;
#app-upload {
  position: relative;
  min-height: 100vh;
  // min-height: 625px;
  overflow: hidden;

  @include until($tablet) {
    height: auto;
    min-height: 625px;
  }
}

.contents-limiter {
  height: 100%;
}

.background-color {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: $bh;
  z-index: -1;

  .radial-gradient {
    content: "";
    position: absolute;
    width: 2244px;
    height: 2244px;
    left: 172px;
    top: 230px;

    @include until($tablet) {
      left: -174px;
      top: -140px;
    }

    background: radial-gradient(
      1122px at 50% 50%,
      $p2 0%,
      $p3 37.5%,
      $p4 70.83%,
      $bh 100%
    );
    filter: opacity(0.25);
    .dithering {
      position: absolute;
      background-image: radial-gradient(
          1122px at 50% 50%,
          transparent 90%,
          $bh 100%
        ),
        url("../assets/noisy_texture_150px.png");
      width: 100%;
      height: 100%;
    }
  }
}

.app-block {
  display: flex;
  height: 100%;
  min-height: 100vh;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
}

.app-navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;

  @include until($tablet) {
    height: 64px;
    width: 100%;
    margin: 0;
    // to override bulma responsiveness in horizental mobile view
  }

  .logo {
    display: inline-block;
    height: 24px;
    margin: 0;
    outline: none;
    cursor: pointer;
    img {
      width: 132px;
      height: 24px;
    }
  }
}

.navbar-right {
  display: flex;
  align-items: center;
}

.app-footer {
  color: $wm;
  padding: 40px 0 40px;

  @include until($tablet) {
    padding: 24px 24px 24px;
  }

  @include until(360px) {
    padding: 16px 24px 16px;
  }

  .right {
    display: flex;
    justify-content: flex-end;

    @include until($tablet) {
      justify-content: flex-start;
    }
  }
  .legal {
    margin: 0 0 0 8px;
    color: $wh;
    transition: color 150ms ease-in-out;
    &:hover {
      color: $wm;
    }
  }
}

.logout {
  color: $wm;
  padding: 7px 0 0 0;
  cursor: pointer;
}

.banner {
  position: fixed;
  bottom: 48px;
  width: 223px;
  height: 363px;
  background: rgba(20, 104, 123, 0.16);

  .relative {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 20px;
    .close {
      position: absolute;
      top: 12px;
      right: 12px;
      cursor: pointer;
      z-index: 1;
    }
  }

  .os-icon {
    display: flex;
    justify-content: space-around;
    padding: 32px 0 24px;
    img {
      width: 52px;
      height: 52px;
    }
  }

  .h6 {
    color: $wh;
    margin: 0 0 17px;
  }

  .list {
    color: $wm;
    margin: 0 0 41px;
    .item {
      display: flex;
      align-items: center;
      margin: 0 0 15px;

      img {
        margin: -1px 8px 0 0;
      }
    }
  }

  .download-link {
    .button-2 {
      color: $p1;
      display: block;
      margin: 0 auto 8px;
      text-align: center;
    }
  }
}

#main {
  margin: 100px 0 88px;
}

.user-avatar {
  position: relative;
  z-index: 2;
  border-radius: 50%;
  display: flex;
  width: 40px;
  height: 40px;
  border: 1px solid $wh-ov08;
  margin: 0 0 0 8px;
  cursor: pointer;
  transition: background-color 150ms ease-in-out;

  &:hover {
    background-color: $wh-ov02;
  }

  &:active {
    background-color: $wh-ov04;
  }

  img {
    margin: auto;
    display: inline-block;
    width: 24px;
    height: 24px;
    opacity: 0.68;
  }
}

.app-launcher {
  position: relative;
  display: flex;
  width: 40px;
  height: 40px;
  transition: background-color 150ms ease-in-out;
  cursor: pointer;
  &:hover {
    background-color: $wh-ov02;
  }
  &:active {
    background-color: $wh-ov04;
  }

  img {
    display: inline-block;
    margin: auto;
    width: 24px;
    height: 24px;
  }
}
</style>
