<template>
  <div id="download" :class="[osName === 'Windows'? 'winstyle': 'macstyle']">
    <div class="contents-limiter">
      <div class="columns is-centered">
        <div class="column is-6">
          <!-- <TheDownloader/> -->
          <div class="download-block">
            <h4 class="h4">
              Thanks for downloading
              <br />BlackHole!
            </h4>
            <!-- <p
            class="subtitle-1"
            >Subscribe to our newsletter to stay up-to-date with the latest version.</p>-->
            <!-- <p class="subtitle-1">
            Put your email here to get access to BlackHole before it's available
            to the public.
            </p>-->
            <!-- <div class="Subscribe" @keyup.enter="subscribe">
            <input
              v-model="email"
              type="email"
              id="EMAIL"
              name="EMAIL"
              autocomplete="off"
              class="body-2"
              placeholder="Email address"
              data-required="true"
              required
            />
            <span class="button-block">
              <input
                @click="subscribe"
                type="button"
                value="Subscribe"
                class="button-2"
                :class="{ loading: isAnimate, done: isDone }"
                :disabled="isAnimate || isDone"
              />
              <span :class="{ spiner: isAnimate }"></span>
              <img
                src="../assets/done_24px.png"
                srcset="../assets/done_24px.png 1x, ../assets/done_24px_2x.png 2x"
                class="done-icon"
                v-show="isDone"
                alt="Done Icon"
              />
            </span>
            <p class="error">{{ errors }}</p>
            </div>-->
            <div class="install-guide" v-show="osName === 'Windows'">
              <p class="subtitle-1 title">Here's how to finish installing BlackHole</p>
              <p class="body-2 step-1">1. Wait for the download to finish</p>
              <div class="step-2">
                <p
                  class="body-2"
                >2. Run the downloaded file. If you got Windows protection message, tap on “More info”</p>
                <img
                  src="../assets/i_guid_win_defender1.png"
                  alt="Setup Guid 1"
                  srcset="../assets/i_guid_win_defender1.png 1x, ../assets/i_guid_win_defender1_2x.png 2x"
                  class="img-step-2"
                />
              </div>
              <div class="step-3">
                <p class="body-2">3. Then tap on “Run anyway” to start the installation</p>
                <img
                  src="../assets/i_guid_win_defender2.png"
                  alt="Setup Guid 2"
                  srcset="../assets/i_guid_win_defender2.png 1x, ../assets/i_guid_win_defender2_2x.png 2x"
                  class="img-step-3"
                />
              </div>
              <p class="body-2 step-4">4. Continue with setup</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import TheDownloader from "../components/TheDownloader";
import axios from "axios";
import { setTimeout } from "timers";
export default {
  name: "download",
  components: {
    // TheDownloader
  },

  data() {
    return {
      email: "",
      errors: "",
      clickTime: 0,
      currentTime: 0,
      isAnimate: false,
      isDone: false,
      osName: ""
    };
  },

  mounted() {
    if (window.location.hash == "#win") {
      this.osName = "Windows"
      setTimeout(() => {
        window.open(
          "https://blackhole.run/dl/win/BlackHole 1.2 Build 171 x64 Setup.exe",
          "_self"
        );
      }, 50);
    } else {
      this.osName = "Mac"
      setTimeout(() => {
        window.open(
          "https://blackhole.run/dl/mac/BlackHole 1.3 Build 173 x64 Setup.zip",
          "_self"
        );
      }, 50);
    }
  },
  methods: {
      validEmail(email) {
      var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return regex.test(email);
    },

    subscribe() {
      if (!this.email) {
        this.errors = "Email required.";

        this.$ga.event({
          eventCategory: "submiter",
          eventAction: "empty_email_error",
          eventLabel: "download_register",
          eventValue: 0
        });
      } else if (!this.validEmail(this.email)) {
        this.errors = "Invalid email address.";

        this.$ga.event({
          eventCategory: "submiter",
          eventAction: "email_validation_error",
          eventLabel: "download_register",
          eventValue: 0
        });
      } else {
        this.clickTime = new Date().getTime();
        this.isAnimate = true;
        let formData = {
          EMAIL: this.email,
          email_address_check: "",
          locale: "en",
          html_type: "simple"
        };
        const encodeForm = data => {
          return Object.keys(data)
            .map(
              key =>
                encodeURIComponent(key) + "=" + encodeURIComponent(data[key])
            )
            .join("&");
        };
        axios
          .post(
            "https://sibforms.com/serve/MUIEAFsZoac3UJJbgEjdG7LEfmdiBGO-bNf3p2NgP5hmLfJI5QHXuS5Yf2VRiYWGEMWiSXsbJMjV5ZWzwf8iaL_HbT5ijVkkhzoRMAK5AirMw1Xc_-GP6788zRiWyCfc9UMe_T7vBB4eqDr_B7MFtXQyWIveOGxWfo-8ZurNDA2V0g7ZGoZ5nXWNClUjShHACxwiaCo7ackJXDCO",
            encodeForm(formData)
          )
          .then(() => {
            this.currentTime = new Date().getTime();
            const timeDiffrence = this.currentTime - this.clickTime;

            if (timeDiffrence > 1000) {
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, timeDiffrence);
            } else {
              this.isAnimate = true;
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, 1000);
            }

            this.$ga.event({
              eventCategory: "submiter",
              eventAction: "email_registering",
              eventLabel: "download_register",
              eventValue: 0
            });
          })
          .catch(reason => {
            this.currentTime = new Date().getTime();
            const timeDiffrence = this.currentTime - this.clickTime;

            if (timeDiffrence > 1000) {
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, timeDiffrence);
            } else {
              this.isAnimate = true;
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, 1000);
            }

            this.$ga.event({
              eventCategory: "submiter",
              eventAction: "email_registering",
              eventLabel: "download_register",
              eventValue: 0
            });
          });
      }
    }
  },

  watch: {
    email: function(newVal, oldVal) {
      if (this.email && this.errors === "Email required.") {
        this.errors = "";
      } else if (
        (!this.email && this.errors === "Invalid email address.") ||
        this.validEmail(this.email)
      ) {
        this.errors = "";
      }
    }
  }
};
</script>

