                  class="image"
                  v-show="tabName === 'dragging'"
                />
              </transition>
              <transition name="slide-fade">
                <img
                  src="../assets/i_guid_win_upload.png"
                  alt="Upload your file in BlackHole"
                  srcset="../assets/i_guid_win_upload.png 1x, ../assets/i_guid_win_upload_2x.png 2x"
                  class="image"
                  v-show="tabName === 'select'"
                />
              </transition>
            </div>
          </div>

          <div ref="blockSize" class="mac" v-show="osName === 'MacOS'">
            <div class="images-block">
              <transition name="slide-fade">
                <img
                  src="../assets/i_guid_mac_upload.png"
                  alt="Upload your file in BlackHole"
                  srcset="../assets/i_guid_mac_upload.png 1x, ../assets/i_guid_mac_upload_2x.png 2x"
                  class="image"
                  v-show="tabName === 'shortcut'"
                />
              </transition>
              <transition name="slide-fade">
                <img
                  src="../assets/i_guid_mac_drag.png"
                  alt="Drag and drop your file into BlackHole tray icon"
                  srcset="../assets/i_guid_mac_drag.png, ../assets/i_guid_mac_drag_2x.png 2x"
                  class="image"
                  v-show="tabName === 'dragging'"
                />
              </transition>
              <transition name="slide-fade">
                <img
                  src="../assets/i_guid_mac_shortcut.png"
                  alt="Use BlackHole shortcuts to make things easier"
                  srcset="../assets/i_guid_mac_shortcut.png 1x, ../assets/i_guid_mac_shortcut_2x.png 2x"
                  class="image"
                  v-show="tabName === 'select'"
                />
              </transition>
            </div>
          </div>
          <div class="navigation subtitle-1">
            <span
              class="items"
              :class="{active: tabName === 'select'}"
              @click="selectTabOnClick"
            >Select</span>
            <span
              class="items"
              :class="{active: tabName === 'dragging'}"
              @click="dragTabOnClick"
            >Drag and drop</span>
            <span
              class="items"
              :class="{active: tabName === 'shortcut'}"
              @click="shortcutTabOnClick"
            >Shortcut</span>
            <span class="movable-indicator" :class="position"></span>
          </div>
        </div>
      </div>
      <div class="columns">
        <div class="column">
          <div class="text-block">
            <span class="h3">2</span>
            <h5 class="h5">Send link</h5>
            <p
              class="body-1"
            >Share the private-link with anyone to access the file – download without registration.</p>
          </div>
        </div>
        <div class="images-block column is-6">
          <img
            src="../assets/i_guid_win_copylink.png"
