<script>
export default {
  data() {
    return {
      osName: "",
      tabName: "select",
      position: ""
    };
  },

  mounted() {
    this.osDetection();
  },

  methods: {
    osDetection() {
      this.osName = "Windows";
      if (
        navigator.appVersion.indexOf("Windows" || "Linux" || "Android") != -1
      ) {
        this.osName = "Windows";
        return;
      }

      if (navigator.appVersion.indexOf("Mac" || "iPad" || "iPhone") != -1) {
        this.osName = "MacOS";
        return;
      }
    },

    selectTabOnClick() {
      this.tabName = "select";
      this.position = "pos1";
    },

    dragTabOnClick() {
      this.tabName = "dragging";
      this.position = "pos2";
    },

    shortcutTabOnClick() {
      this.tabName = "shortcut";
      this.position = "pos3";
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

#workflow {
  padding: 160px 0 160px;
  flex-direction: column;

  @include until($tablet) {
    padding: 128px 0 128px;
  }

  .title {
    // columns has display:flex declaration
    align-self: center;
    h4 {
      margin: 0 16px 65px;
      color: $wh;
      @include until($tablet) {
        margin: 0 0 81px;
      }
    }
  }
}

.mainbody {
  @include until($tablet) {
    padding: 0;
  }
  .columns {
    margin: 0 0 101px;
    &:last-child {
      margin: 0;
    }
    @include until($tablet) {
      margin: 0 0 91px;
    }
  }
}

.text-block {
  width: 75%;
  margin: 0 0 0 25%;
  @include until($tablet) {
    width: 100%;
    margin: 0;
  }

  .h3 {
    display: inline-block;
    color: $bl;
    margin: 0 0 45px;
    @include until($tablet) {
      margin: 0 0 15px;
    }
  }

  .h5 {
    color: $wh;
    margin: 0 0 6px;
  }

  .body-1 {
    color: $wm;
    margin: 0 40px 20px 0;
    @include until($tablet) {
      margin: 0 0 23px;
    }
  }

  .first {
    margin: 20px 0 45px;
    @include until($tablet) {
      margin: 0 0 15px;
    }
  }
}

.gallery-block {
  .windows,
  .mac {
    max-width: 448px;
    // height: 208px;
    overflow: hidden;

    .images-block {
      display: flex;
      .image {
        display: inline-block;
        height: auto;
        max-width: 100%;
      }
    }
  }
}

.navigation {
  position: relative;
  max-width: 448px;
  padding: 20px 0 0;
  border-bottom: 1px solid fade-out($color: $wh, $amount: 0.92);

  .items {
    display: inline-block;
    color: $wl;
    margin: 0 33px 0 0;
    padding: 0 0 10px;
    cursor: pointer;
    transition: color 300ms cubic-bezier(0.23, 1, 0.32, 1);
    @include until($tablet) {
      margin: 0 24px 0 0;
      &:nth-child(3n) {
        margin: 0;
      }
    }
    &:nth-child(3n) {
      margin: 0;
    }
  }

  .active {
    color: $wm;
  }

  .movable-indicator {
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 57px;
    height: 2px;
    background-color: $wm;
    transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1),
      width 300ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  .pos1 {
    transform: translate(0, 0);
    width: 54px;
    @include until($tablet) {
      transform: translate(0, 0);
    }
  }

  .pos2 {
    transform: translate(87px, 0);
    width: 123px;
    @include until($tablet) {
      transform: translate(78px, 0);
    }
  }

  .pos3 {
    transform: translate(243px, 0);
    width: 76px;
    @include until($tablet) {
      transform: translate(225px, 0);
    }
  }
}

// .slide-fade-enter-active {
//   transition: all 300ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
// }
// .slide-fade-leave-active {
//   transition: all 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
// }
// .slide-fade-enter,
// .slide-fade-leave-to {
//   opacity: 0;
// }

.slide-fade-enter-active {
  transition: all 250ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
}
.slide-fade-leave-active {
  transition: all 250ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.slide-fade-enter {
  transform: translate(448px, 0);
  opacity: 0;
}

.slide-fade-enter-to {
  transform: translate(0, 0);
  opacity: 1;
}

.slide-fade-leave-to {
  transform: translate(0, 0);
  opacity: 1;
}

.slide-fade-leave-to {
  transform: translate(-448px, 0);
  opacity: 0;
}

