<template>
  <div id="signup">
    <div class="contents-limiter">
      <div class="columns">
        <div class="column is-offset-3">
          <div class="signup-block">
            <h4 class="h4">Login.</h4>
            <p class="subtitle-1">
              Please continue with Blockstack
              <sup>1</sup> to
              log in or create a new account.
            </p>
            <button
              class="signup-button"
              :class="{ 'signupDisable' : isDisabled}"
              :disabled="isDisabled"
              @click="signUpToBlockstack"
            >
              <img
                src="../assets/i_bhblockstack_login.png"
                srcset="../assets/i_bhblockstack_login.png 1x, ../assets/i_bhblockstack_login_2x.png 2x"
                alt="Navigate to BlockStack button"
              />
            </button>
          </div>
          <p class="body-3">
            1. Blockstack provides Distributed Storage that allows you to manage your
            own identity in the entire ecosystem and store information in your provider
            of choice. This is very different from traditional applications where the
            information and authentication system is closed behind the companies servers.
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { userSession } from "../user.js";
import { log } from "util";

export default {
  name: "signup",
  data() {
    return {
      isDisabled: false
    };
  },
  methods: {
    signUpToBlockstack() {
      this.isDisabled = true;

      userSession.redirectToSignIn(
        window.location.origin + "/web/welcome",
        window.location.origin + "/manifest.json",
        ["store_write", "publish_data"]
      );

      // userSession.redirectToSignIn(
      //   "https://blackhole.run/web/select-platform",
      //   "https://blackhole.run/manifest.json",
      //   ["store_write", "publish_data"]
      // );
    }
  }
};
</script>


<style lang="scss" scoped>
@include override-gaps;

#signup {
  padding: 208px 0 40px;
  height: calc(100vh - 106px);
  min-height: 721px;
}

.contents-limiter,
.columns,
.column {
  height: 100%;
}

.column {
  display: flex;
  flex-direction: column;
}

.signup-block {
  width: 67%;

  .h4 {
    color: $wh;
    margin: 0 0 8px;
  }

  .subtitle-1 {
    color: $wm;
    margin: 0 0 131px;
    sup {
      font-size: 12px;
      line-height: 0;
      margin: 0 0 0 -4px;
    }
  }

  .signup-button {
    width: 280px;
    height: 40px;
    cursor: pointer;
  }

  .signupDisable {
    opacity: 0.38;
    cursor: auto;
  }
}

.body-3 {
  color: $wl;
  margin: auto 0 0;
}
</style>
